import React, { useEffect, useMemo, useState } from "react";

import { IJob } from "@ilabs/core";
import { IZoningMetadata, Zoning } from "@ilabs/ui";

type Props = Readonly<{
  job: IJob;
  token: string;
}>;

const META_ROLE = "zoning-pagesmeta-json";
const ZONES_IN_ROLE = "zoning-reviewed-json";
const LO_RES_IMAGE_ROLE = "zoning-page-png";
const HI_RES_IMAGE_ROLE = "zoning-pagehr-png";

export const ZoningReview = ({ job, token }: Props) => {
  const [contents, setContents] = useState<IZoningMetadata | null>(null);
  const [error, setError] = useState<Error | null>(null);
  const apiKey = useMemo(() => {
    try {
      const decoded = atob(token);
      if (decoded.charAt(decoded.length - 1) === ":") {
        return decoded.substr(0, decoded.length - 1);
      }
      setError(new Error("Unable to decode api key."));
      return null;
    } catch (err) {
      setError(err);
      return null;
    }
  }, [token]);
  useEffect(() => {
    if (apiKey === null) {
      return;
    }
    const meta = job.contents.find(c => c.role === META_ROLE);
    if (!meta) {
      setError(new Error("Job does not have zoning metadata content."));
      return;
    }
    fetch(meta.uri, {
      headers: {
        Authorization: `Basic ${token}`,
      },
      method: "GET",
    })
      .then(res => res.json())
      .then(res => {
        setContents(res);
      })
      .catch(setError);
  }, [apiKey, job.id, token]);
  const [
    pageConfidences,
    pageDimensions,
    highResImageURIs,
    lowResImageURIs,
    pageZoneIds,
    pageZoneURIs,
  ] = useMemo(() => {
    if (apiKey === null) {
      return [[], [], [], [], [], []];
    }
    if (contents === null || !Array.isArray(contents.pages)) {
      return [[], [], [], [], [], []];
    }
    let highResImages = job.contents
      .filter(c => c.role === HI_RES_IMAGE_ROLE)
      .map(c => `${c.uri}?token=${apiKey}`);
    if (highResImages.length < 1) {
      highResImages = job.contents
        .filter(c => c.role === LO_RES_IMAGE_ROLE)
        .map(c => `${c.uri}?token=${apiKey}`);
    }
    return [
      job.contents
        .filter(c => c.role === LO_RES_IMAGE_ROLE)
        .map((c, i) => Number.parseFloat(job.metadata[`page_confidence_${i}`])),
      contents.pages
        .map(p => {
          if (typeof p.images !== "object" || p.images === null || Array.isArray(p.images)) {
            return { height: 0, width: 0 };
          }
          const { height, width } = p.images;
          return { height, width };
        })
        .filter(s => !isNaN(s.height) && s.height > 0 && !isNaN(s.width) && s.width > 0),
      highResImages,
      job.contents.filter(c => c.role === LO_RES_IMAGE_ROLE).map(c => `${c.uri}?token=${apiKey}`),
      job.contents.filter(c => c.role === ZONES_IN_ROLE).map(c => c.id),
      job.contents.filter(c => c.role === ZONES_IN_ROLE).map(c => `${c.uri}?token=${apiKey}`),
    ];
  }, [job.id, contents]);
  if (error) {
    return <div>{error.message}</div>;
  } else if (contents === null) {
    return <div>Loading...</div>;
  }
  return (
    <Zoning
      fetchJobContent={async contentId => {
        const content = job.contents.find(c => c.id === contentId);
        if (!content) {
          throw new Error("Content not found.");
        }
        const result = await fetch(content.uri, {
          headers: { Authorization: `Basic ${token}` },
          method: "GET",
        });
        return result.json();
      }}
      highConfidenceThreshold={Number.parseFloat(job.metadata.high_confidence_threshold) || 1.0}
      highResImageURIs={highResImageURIs}
      jobId={job.id}
      lowResImageURIs={lowResImageURIs}
      onSave={async () => {
        console.warn("This should never be called.");
      }}
      pageConfidences={pageConfidences}
      pageDimensions={pageDimensions}
      pageZoneIds={pageZoneIds}
      pageZoneURIs={pageZoneURIs}
      review={true}
      saving={false}
      startingPage={0}
    />
  );
};
ZoningReview.displayName = "ZoningReview";
